export default {
  data () {
    return {
      // Start date/time
      start_time: null,
      start_date: new Date().toISOString().substr(0, 10),
      start_modal_date: false,
      start_modal_time: false,
      // // End date/time
      end_time: null,
      end_date: new Date().toISOString().substr(0, 10),
      end_modal_date: false,
      end_modal_time: false,
      // Set Appointment Props
      firstname: '',
      lastname: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters',
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      // MESSAGES
      valid: true,
      success: false,
      error: false,
      errors: []
    }
  },
  methods: {
    /**--------------------------------------------
     * Create Appointment
     * --------------------------------------------
     */
    createAppointment() {
      const params = {
        firstname: this.firstname,
        lastname: this.lastname,
        email: this.email,
        reservationtime: `${new Date(this.start_date + ' ' + this.start_time).toUTCString()} - ${new Date(this.end_date + ' ' +this.end_time).toUTCString()}`
      };

      fetch('/api/appointments', {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify(params)
      })
      .then(res => res.json())
      .then(data => {
        console.log(data)

        if(data.status == 201) {
          this.success = true;
          // Clear data
          this.start_date = null;
          this.start_time = null;
          this.end_date = null;
          this.end_time = null;
          setTimeout(() => {
            this.success = false;
          }, 5000)
        }

      })
      .catch(err => console.log(err))
    },

    /**--------------------------------------------
     * Form Validation
     * --------------------------------------------
     */
    checkForm(e) {
      if (
        this.email &&
        this.firstname &&
        this.lastname &&
        this.start_time && 
        this.start_date && 
        this.end_time &&
        this.end_date
      ) {
        return true;
      }

      this.errors = [];

      if (!this.email) {
        this.errors.push('Email required.');
      }
      if (!this.firstname) {
        this.errors.push('First name required.');
      }
      if (!this.lastname) {
        this.errors.push('Last name required.');
      }
      if (!this.start_time) {
        this.errors.push('Start time required.');
      }
      if (!this.start_date) {
        this.errors.push('Start date required.');
      }
      if (!this.end_time) {
        this.errors.push('End time required.');
      }
      if (!this.end_date) {
        this.errors.push('End date required.');
      }

      console.log(this.errors)
      e.preventDefault();
    }
  }
}
